.scroll-to-top {
  width: 20px;
  height: 20px;
  cursor: pointer;
  transition: transform var(--anim), background-color var(--anim);
  background-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  margin-left: auto;
  display: flex;
}

@media screen and (min-width: 768px) {
