    width: 40px;
    height: 40px;
  }
}

.scroll-to-top:hover, .scroll-to-top:focus {
  background-color: rgba(0, 0, 0, 0);
  transform: scale(1.05);
}

.arrow-top {
  fill: var(--bgc-accent);
  opacity: .5;
  width: 30px;
  height: 30px;
}

