@keyframes heartbeat {
    from {
        transform: scale(1);
        transform-origin: center center;
        animation-timing-function: ease-out;
    }

    10% {
        transform: scale(0.85);
        animation-timing-function: ease-in;
    }

    17% {
        transform: scale(0.95);
        animation-timing-function: ease-out;
    }

    33% {
        transform: scale(0.85);
        animation-timing-function: ease-in;
    }

    45% {
        transform: scale(1);
        animation-timing-function: ease-out;
    }
}